import { useEffect, useState } from "react";
import { useParams, useHistory } from "react-router-dom";
import { getMealById } from "../api";
import { Preloader } from "../components/Preloader";

function Recipe() {
  const { id } = useParams();
  const [recipe, setRecipe] = useState({});
  const { goBack } = useHistory();

  useEffect(() => {
    getMealById(id).then((data) => setRecipe(data.meals[0]));
  }, [id]); /* ÐÐ°Ð¶Ð´ÑÐ¹ ÑÐ°Ð· ÐºÐ¾Ð³Ð´Ð° Ð¼ÐµÐ½ÑÐµÑÑÑÑ Ð°Ð¹Ð´Ð¸ ÑÑÐ°Ð±Ð°ÑÑÐ²Ð°ÐµÑ useEffect */

  return (
    <>
      {!recipe.idMeal ? (
        <Preloader />
      ) : (
        <div className="recipe">
          <img src={recipe.strMealThumb} alt={recipe.strMeal} />
          <h2>{recipe.strMeal}</h2>
          <h6>Category: {recipe.strCategory}</h6>
          {recipe.strArea ? <h6>Area: {recipe.strArea}</h6> : null}
          <p>{recipe.strInstructions}</p>
          <table className="responsive-table">
            <thead>
              <tr>
                <th>Ingredient</th>
                <th>Measure</th>
              </tr>
            </thead>
            <tbody>
              {Object.keys(recipe).map((key) => {
                if (key.includes("Ingredient") && recipe[key]) {
                  return (
                    <tr key={key}>
                      <td>{recipe[key]}</td>
                      <td>{recipe[`strMeasure${key.slice(13)}`]}</td>
                    </tr>
                  );
                }
                return null;
              })}
            </tbody>
          </table>{/* Ð²ÐµÑÑ Ð¾Ð±ÑÐµÐºÑ Ð¼Ñ Ð¿ÐµÑÐµÐ±Ð¸ÑÐ°ÐµÐ¼ ÐºÐ»ÑÑÐ¸ Ð¸ Ð¿ÑÐ¾Ð²ÐµÑÑÐµÐ¼ ÐµÑÑÑ Ð»Ð¸ ÑÐ°Ð¼ "ingridient" Ð¿ÑÐ¸ Ð¿Ð¾Ð¼Ð¾ÑÐ¸ includes Ð¸ ÐµÑÑÑ Ð»Ð¸ ÑÐ°Ð¼ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð¿ÑÐ¸ Ð¿Ð¾Ð¼Ð¾ÑÐ¸ [key] */}

          {recipe.strYoutube ? (
            <div className="row">
              <h5 style={{ margin: "2rem 0 1.5rem" }}>Video Recipe</h5>
              <iframe
                title={id}
                src={`https://www.youtube.com/embed/${recipe.strYoutube.slice(
                  -11
                )}`}
                allowfullscreen
              />
            </div>
          ) : null}
        </div>
      )}
      <button className="btn" onClick={goBack}>
        Go back
      </button>
    </>
  );
}

export { Recipe };
